* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  margin: 0;
}

.country-main {
  display: flex;
  align-items: center;
}

.country-info_extended {
  list-style: none;
}

.country-info_name {
  margin: 10px;
}
.country-info_text {
  font-size: 16px;
  font-weight: 600;
}
.country-info_description {
  font-size: 18px;
  font-weight: 400;
}
.hide {
  display: none;
}
